// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of, Observable } from 'rxjs';
import { delay, map, startWith } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';
import { ReadingResultModel } from '../reading-result.model';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-readings-results-edit-dialog',
	templateUrl: 'readings-results-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ReadingsResultsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	userReading: ReadingResultModel;
	readingForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	employees = [];
	fuelTypes = [];
	gasPumps = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	myControl = new FormControl();
	filteredOptions: Observable<any[]>;
	selectedPumpId = '';
	isFinalReading = false;
	gsReadings = [];
	numberOfReadings = [];
	oldGasolinePumpId = '';
	// TODO: get using api
	shifts = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<ReadingsResultsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<ReadingsResultsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.userReading = this.data.reading;
		this.employees = this.data.employees;
		this.shifts = this.data.shifts;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		// this.getShifts();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	private _filter(value: string): any[] {
		const filterValue = value.toLowerCase();

		return this.gasPumps.filter(gasPump => gasPump.name.toLowerCase().includes(filterValue));
	}

	createForm() {
		let shiftStartDate = this.userReading.date;
		if (this.userReading.id > 0)
			shiftStartDate = new Date(+new Date(this.userReading.date) - this.tzoffset).toISOString().substr(0, 16);

		this.readingForm = this.fb.group({
			id: [this.userReading.id, Validators.required],
			userId: [this.userReading.userId, Validators.required],
			shift: [this.userReading.shiftId, Validators.required],
			discounts: [this.userReading.discounts, ''],
			coupons: [this.userReading.coupons, ''],
			oils: [this.userReading.oils, ''],
			checks: [this.userReading.checks, ''],
			readingDate: [shiftStartDate, Validators.required],
			deliveredMoneyByEmployee: [this.userReading.deliveredMoneyByEmployee, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.userReading.id > 0) {
			return `تعديل القراءة للموظف '${this.userReading.userName}'`;
		}

		return 'اضافة قراءة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.readingForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	// getShifts() {
	// 	this.http.get(
	// 		globalVariables.baseUrl + 'api/Shifts',
	// 		{ headers: this.headers }
	// 	).subscribe((res: any) => {
	// 		this.shifts = res.data.map(item => {
	// 			return {
	// 				name: item.name,
	// 				id: item.id
	// 			};
	// 		});
	// 	});
	// }

	resetGSReading() {
		this.numberOfReadings.splice(1);
	}

	/**
	 * Returns prepared customer
	 */
	prepareReading(): ReadingResultModel {
		const controls = this.readingForm.controls;
		const _reading = new ReadingResultModel();
		_reading.id = this.userReading.id;
		_reading.userId = controls.userId.value;
		_reading.shift = controls.shift.value;
		_reading.discounts = controls.discounts.value;
		_reading.coupons = controls.coupons.value;
		_reading.oils = controls.oils.value;
		_reading.checks = controls.checks.value;
		_reading.date = controls.readingDate.value;
		_reading.deliveredMoneyByEmployee = controls.deliveredMoneyByEmployee.value;

		return _reading;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.readingForm.controls;

		/** check form */
		if (this.readingForm.invalid) {
			console.log(this.readingForm);

			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
		}

		if (this.hasFormErrors)
			return;

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		}
	}

	/**
	 * Update customer
	 *
	 * @param _reading: ReadingResultModel
	 */
	updateReading(_reading: ReadingResultModel) {
		let readingOutcomeDate = new Date(+new Date(_reading.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/ShiftsReadings/Employees/Outcomes',
			{
				id: _reading.id,
				moneyDeliveredByEmployee: +_reading.deliveredMoneyByEmployee,
				employeeId: +_reading.userId,
				shiftId: +_reading.shift,
				discounts: +_reading.discounts,
				coupons: +_reading.coupons,
				oils: +_reading.oils,
				checks: +_reading.checks,
				readingOutcomeDate,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _reading, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل القراءة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
