// Angular
import {Component, OnDestroy, OnInit} from '@angular/core';
// Lodash
import {shuffle} from 'lodash';
// Services
// Widgets model
import {LayoutConfigService, SparklineChartOptions} from '../../../core/_base/layout';
import {Subscription} from 'rxjs';
import {Store} from '@ngrx/store';
import {AppState} from '../../../core/reducers';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../globalVariables';

@Component({
	selector: 'gas-readings',
	templateUrl: 'readings.component.html',
	styleUrls: ['readings.component.scss'],
})
export class ReadingsComponent implements OnInit, OnDestroy {
	employees = [];
	fuelTypes = [];
	gasPumps = [];
	shifts = [];

	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	constructor(private layoutConfigService: LayoutConfigService, private store: Store<AppState>,
				private http: HttpClient) {
	}

	ngOnInit(): void {
		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.getEmployees();
		this.getFuelTypes();
		this.getGasPumps();
		this.getShifts();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	/** ACTIONS */
	getFuelTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=fuel',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelTypes = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				}
			});
		});
	}

	getEmployees() {
		this.http.get(
			globalVariables.baseUrl + 'api/Employee', // instead of /Users
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.employees = res.data.map(item => {
				return {
					name: item.name,
					id: item.userId
				}
			});
		});
	}

	getGasPumps() {
		this.http.get(
			globalVariables.baseUrl + 'api/GasolinePumps',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.gasPumps = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				}
			});
		});
	}

	getShifts() {
		this.http.get(
			globalVariables.baseUrl + 'api/Shifts',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.shifts = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});
		});
	}
}


