export class ReadingResultModel {
	id: number;
	date: string;
	userId: string;
	userName: string;
	shiftId: string;
	shift: string;
	virtualSummation: string;
	discounts: string;
	coupons: string;
	oils: string;
	checks: string;
	deliveredMoneyByEmployee: string;
	totalCashEmployeeShouldDeliver: string;
	difference: string;

	clear() {
		this.id = 0;
		this.date = new Date().toISOString().substr(0, 16);
	}
}
