// Angular
import { Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, Input } from '@angular/core';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, delay, take } from 'rxjs/operators';
import { fromEvent, merge, Subscription, of } from 'rxjs';
// Translate Module
import { TranslateService } from '@ngx-translate/core';
// NGRX
import { Store, ActionsSubject } from '@ngrx/store';
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';
// Services and Models
import { UserReadingModel, OneCustomerDeleted, ManyCustomersDeleted } from '../../../../../core/e-commerce';
// Components
import { UsersReadingsEditDialogComponent } from '../users-readings-edit/users-readings-edit.dialog.component';
// Http
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/compgetItemCssClassByStatusonents/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-users-readings',
	templateUrl: 'users-readings.component.html',
	styleUrls: ['users-readings.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,

})
export class UsersReadingsComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource: MatTableDataSource<UserReadingModel>;
	displayedColumns = ['readingDate', 'gpNumber', 'userName', 'shift', 'shiftInitalReading', 'shiftFinalReading', 'amount', 'unitPrice',
		'totalPrice', 'actions']; // 'coupons', 'discounts', 'shiftStartDate', 'shiftEndDate',
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	@ViewChild('sort1', { static: true }) sort: MatSort;
	isLoadingResults = false;
	// Filter fields
	@ViewChild('searchInput', { static: true }) searchInput: ElementRef;
	selectedEmployee = '';
	selectedFuelType = '';
	selectedGasPump = '';
	selectedShift = '';
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	dateFrom = new Date(+new Date(this.date.getFullYear(), this.date.getMonth(), 1) - this.tzoffset).toISOString().slice(0, 16);
	dateTo = new Date(+new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0) - this.tzoffset).toISOString().slice(0, 16);
	// Filter data
	@Input() employees = [];
	@Input() fuelTypes = [];
	@Input() gasPumps = [];
	@Input() shifts = [];
	// Selection
	selection = new SelectionModel<UserReadingModel>(true, []);
	customersResult: UserReadingModel[] = [];
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';
	// Language
	languageText = {
		empty: 'لا يوجد بيانات لعرضها',
		searching: 'جار البحث ...',
		search: 'بحث',
		filterEmptyMessage: 'لا يوجد بيانات مطابقة ل ',
		confirm: 'تأكيد',
		close: 'الغاء',
		add: 'اضافة',
		itemsPerPage: 'عدد الصفوف بالصفحة:',
		firstPage: 'الصفحة الأولى',
		lastpage: 'الصفحة الأخيرة',
		previousPage: 'الصفحة السابقة',
		nextPage: 'الصفحة التالية'
	};
	// Symmary data
	totalAmount = 0;
	totalPrice = 0;

	/**
	 * Component constructor
	 *
	 * @param dialog: MatDialog
	 * @param snackBar: MatSnackBar
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param translate: TranslateService
	 * @param store: Store<AppState>
	 * @param http
	 */
	constructor(
		public dialog: MatDialog,
		public snackBar: MatSnackBar,
		private layoutUtilsService: LayoutUtilsService,
		private translate: TranslateService,
		private store: Store<AppState>,
		private http: HttpClient
	) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		// Init DataSource
		this.dataSource = new MatTableDataSource();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.loadUsersReadingsList();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	arabicRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `0 من ${length}`;
		}

		length = Math.max(length, 0);

		const startIndex = page * pageSize;

		// If the start index exceeds the list length, do not try and fix the end index to the end.
		const endIndex = startIndex < length ?
			Math.min(startIndex + pageSize, length) :
			startIndex + pageSize;

		return `${startIndex + 1} - ${endIndex} من ${length}`;
	};

	/**
	 * Load UsersReadings List from service through data-source
	 */
	loadUsersReadingsList() {
		this.totalAmount = 0;
		this.totalPrice = 0;
		this.isLoadingResults = true;
		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?FromDate=' + dateFrom + '&ToDate=' + dateTo;
		if (this.selectedEmployee) {
			url += '&userId=' + this.selectedEmployee;
		}
		if (this.selectedFuelType) {
			url += '&CategoryId=' + this.selectedFuelType;
		}
		if (this.selectedGasPump) {
			url += '&GPId=' + this.selectedGasPump;
		}
		if (this.selectedShift) {
			url += '&ShiftId=' + this.selectedShift;
		}
		this.http.get(
			globalVariables.baseUrl + 'api/ShiftsReadings/Employees/GPs' + url,
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.isLoadingResults = false;
				this.dataSource = new MatTableDataSource(
					res.data.map(item => {
						return {
							...item,
							gpNumber: item.gasolinePumpName,
							userId: item.employeeId,
							userName: item.employeeName,
							shift: item.shiftName,
							shiftInitalReading: item.initialReading,
							shiftFinalReading: item.finalReading,
						};
					})
				);
				this.dataSource.data.forEach(element => {
					this.totalAmount += +element.amount;
					this.totalPrice += +element.totalPrice;
				});
				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			}, (err) => {
				this.isLoadingResults = false;

				this.dataSource.data = [];

				this.dataSource.data.forEach(element => {
					this.totalAmount += +element.amount;
					this.totalPrice += +element.totalPrice;
				});

				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			});
	}

	/** ACTIONS */

	/**
	 * Delete Reading
	 *
	 * @param _item: UserReadingModel
	 */
	deleteReading(_item: UserReadingModel) {
		const _title: string = 'حذف القراءة';
		const _description: string = 'هل أنت متأكد من حذف القراءة؟';
		const _waitDesciption: string = 'جاري الحذف';
		const _deleteMessage = 'لقد تم الحذف';

		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			// TODO: Delete action (API)
			this.http.delete(
				globalVariables.baseUrl + 'api/ShiftsReadings/' + _item.id,
				{ headers: this.headers }
			)
				.subscribe((res: any) => {
					this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
					this.loadUsersReadingsList();
				}, err => {
					let msg = 'فشل في حذف القراءة';
					if (err.error) {
						msg = err.error.msg;
					}

					this.layoutUtilsService.showActionNotification(msg);
				});
		});
	}

	/**
	 * Show add reading dialog
	 */
	addReading() {
		const newReading = new UserReadingModel();
		newReading.clear(); // Set all defaults fields
		this.editReading(newReading);
	}

	/**
	 * Show Edit reading dialog and save after success close result
	 * @param reading: UserReadingModel
	 */
	editReading(reading: UserReadingModel) {
		let saveMessageTranslateParam = reading.id <= 0 ? 'تم الاضافة بنجاح' : 'تمت التعديل بنجاح';
		const _saveMessage = saveMessageTranslateParam;
		const _messageType = reading.id > 0 ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(UsersReadingsEditDialogComponent, {
			data: {
				reading,
				gasPumps: this.gasPumps,
				employees: this.employees,
				fuelTypes: this.fuelTypes,
				shifts: this.shifts
			},
			disableClose: true
		});
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType);
			this.loadUsersReadingsList();
		});
	}

	/**
	 * Get file name of the exported excel file
	 */
	getExcelFileName() {
		return 'قراءات الموظفين' + '-' + new Date().toISOString().slice(0, 10);
	}

	/**
	 * Reset Filters
	 */
	resetFilters() {
		this.selectedEmployee = '';
		this.selectedFuelType = '';
		this.selectedGasPump = '';
		this.selectedShift = '';
		this.dateFrom = new Date(+new Date(this.date.getFullYear(), this.date.getMonth(), 1) - this.tzoffset).toISOString().slice(0, 16);
		this.dateTo = new Date(+new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0) - this.tzoffset).toISOString().slice(0, 16);
	}
}
