// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of, Observable } from 'rxjs';
import { delay, map, startWith } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { UserReadingModel, selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

class GasPumpReading {
	gpNumber: string;
	shiftInitalReading: string;
	shiftFinalReading: string;
	validator: FormGroup;
	filteredOptions: Observable<any[]>;
}

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-users-readings-edit-dialog',
	templateUrl: 'users-readings-edit.dialog.component.html',
	styles: [`
		.close {
			position: absolute; top: 1px; left: 1px;
			cursor: pointer;
		}

		.close:hover {
			color: red;
		}
	`],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class UsersReadingsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	userReading: UserReadingModel;
	readingForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	employees = [];
	fuelTypes = [];
	gasPumps = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	myControl = new FormControl();
	filteredOptions: Observable<any[]>;
	selectedPumpId = '';
	isFinalReading = false;
	gsReadings = [];
	numberOfReadings = [];
	oldGasolinePumpId = '';
	// TODO: get using api
	shifts = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.userReading = this.data.reading;
		this.oldGasolinePumpId = this.userReading.gasolinePumpId;
		this.employees = this.data.employees;
		this.fuelTypes = this.data.fuelTypes;
		this.gasPumps = this.data.gasPumps;
		this.shifts = this.data.shifts;

		this.gasPumps.forEach(() => {
			let gsReading = new GasPumpReading();
			gsReading.validator = this.fb.group({
				gpNumber: new FormControl(gsReading.gpNumber, Validators.required),
				shiftInitalReading: [gsReading.shiftInitalReading, Validators.required],
				shiftFinalReading: [gsReading.shiftFinalReading, Validators.required],
			});

			gsReading.filteredOptions = gsReading.validator.get('gpNumber').valueChanges
				.pipe(
					startWith(''),
					map((value: any) => this._filter(value))
				);

			this.gsReadings.push(gsReading)
		});

		this.numberOfReadings = [1];
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		// this.getShifts();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	private _filter(value: string): any[] {
		const filterValue = value.toLowerCase();

		return this.gasPumps.filter(gasPump => gasPump.name.toLowerCase().includes(filterValue));
	}

	createForm() {
		let shiftStartDate = this.userReading.shiftStartDate;
		if (this.userReading.id <= 0)
			shiftStartDate = new Date(+new Date(this.userReading.shiftStartDate) - this.tzoffset).toISOString().substr(0, 16);

		this.readingForm = this.fb.group({
			id: [this.userReading.id, Validators.required],
			gpNumber: new FormControl(this.userReading.gpNumber, this.userReading.id > 0 ? Validators.required : null),
			shiftInitalReading: [this.userReading.shiftInitalReading, this.userReading.id > 0 ? Validators.required : ''],
			shiftFinalReading: [this.userReading.shiftFinalReading, this.userReading.id > 0 ? Validators.required : ''],
			userId: [this.userReading.userId, this.userReading.id <= 0 ? Validators.compose([Validators.required, Validators.min(1)]) : ''],
			shift: [this.userReading.shift, this.userReading.id <= 0 ? Validators.required : ''],
			discounts: [this.userReading.discounts, ''],
			coupons: [this.userReading.coupons, ''],
			oils: [this.userReading.oils, ''],
			checks: [this.userReading.checks, ''],
			readingDate: [shiftStartDate, this.userReading.id <= 0 ? Validators.required : ''],
			deliveredMoneyByEmployee: [this.userReading.deliveredMoneyByEmployee, this.userReading.id <= 0 ? Validators.required : ''],
		});

		if (this.userReading.id > 0)
			this.filteredOptions = this.readingForm.get('gpNumber').valueChanges
				.pipe(
					startWith(''),
					map((value: any) => this._filter(value))
				);
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.userReading.id > 0) {
			return `تعديل القراءة للموظف '${this.userReading.userName}'`;
		}

		return 'اضافة قراءة';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.readingForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	// getShifts() {
	// 	this.http.get(
	// 		globalVariables.baseUrl + 'api/Shifts',
	// 		{ headers: this.headers }
	// 	).subscribe((res: any) => {
	// 		this.shifts = res.data.map(item => {
	// 			return {
	// 				name: item.name,
	// 				id: item.id
	// 			};
	// 		});
	// 	});
	// }

	deleteGSReading(index) {
		this.numberOfReadings.splice(index, 1);
	}

	resetGSReading() {
		this.numberOfReadings.splice(1);
	}

	/**
	 * Returns prepared customer
	 */
	prepareReading(): UserReadingModel {
		const controls = this.readingForm.controls;
		const _reading = new UserReadingModel();
		_reading.id = this.userReading.id;
		_reading.userId = controls.userId.value;
		_reading.shift = controls.shift.value;
		_reading.discounts = controls.discounts.value;
		_reading.coupons = controls.coupons.value;
		_reading.oils = controls.oils.value;
		_reading.checks = controls.checks.value;
		_reading.readingDate = controls.readingDate.value;
		_reading.deliveredMoneyByEmployee = controls.deliveredMoneyByEmployee.value;
		_reading.gpNumber = controls.gpNumber.value;
		_reading.shiftInitalReading = controls.shiftInitalReading.value;
		_reading.shiftFinalReading = controls.shiftFinalReading.value;
		// _reading.amount = controls.amount.value;
		// _reading.totalPrice = controls.totalPrice.value;
		// _reading.unitPrice = controls.unitPrice.value;
		// _reading.shiftStartDate = this.userReading.id > 0 ? controls.shiftStartDate.value : controls.readingDate.value;
		// _reading.shiftEndDate = controls.shiftEndDate.value;

		return _reading;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.readingForm.controls;

		/** check form */
		if (this.readingForm.invalid) {
			console.log(this.readingForm);

			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
		}

		if (this.userReading.id <= 0)
			for (let index = 0; index < this.numberOfReadings.length; index++) {
				const element = this.gsReadings[index];

				const controls = element.validator.controls;

				/** check form */
				if (element.validator.invalid) {
					Object.keys(controls).forEach(controlName =>
						controls[controlName].markAsTouched()
					);

					this.hasFormErrors = true;
				}
			}

		if (this.hasFormErrors)
			return;

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update customer
	 *
	 * @param _reading: UserReadingModel
	 */
	updateReading(_reading: UserReadingModel) {
		let readingDate = new Date(+new Date(_reading.readingDate) - this.tzoffset).toISOString().slice(0, 16);

		if (this.selectedPumpId != '') {
			_reading.gpNumber = this.selectedPumpId;
		} else {
			_reading.gpNumber = this.oldGasolinePumpId;
		}

		this.http.put(
			globalVariables.baseUrl + 'api/ShiftsReadings',
			{
				// ..._reading,
				id: _reading.id,
				oldGasolinePumpId: this.oldGasolinePumpId,
				newGasolinePumpId: +_reading.gpNumber,
				initialReading: +_reading.shiftInitalReading,
				finalReading: +_reading.shiftFinalReading,

				// deliveredMoneyByEmployee: +_reading.deliveredMoneyByEmployee,
				// userId: +_reading.userId,
				// shiftId: +_reading.shift,
				// gasolinePumpId: +_reading.gpNumber,
				// shiftInitalReading: +_reading.shiftInitalReading,
				// shiftFinalReading: +_reading.shiftFinalReading,
				// discounts: +_reading.discounts,
				// coupons: +_reading.coupons,
				// oils: +_reading.oils,
				// checks: +_reading.checks,
				// readingDate,
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _reading, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل القراءة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create customer
	 *
	 * @param _reading: UserReadingModel
	 */
	createReading(_reading: UserReadingModel) {
		let readingDate = new Date(+new Date(_reading.readingDate) - this.tzoffset).toISOString().slice(0, 16);

		let readings = [];
		for (let index = 0; index < this.numberOfReadings.length; index++) {
			const element = this.gsReadings[index];

			readings.push({
				gasolinePumpId: element.gpNumber,
				initialReading: element.shiftInitalReading,
				finalReading: element.shiftFinalReading
			});
		}

		this.http.post(
			globalVariables.baseUrl + 'api/ShiftsReadings',
			{
				// ..._reading,
				moneyDeliveredByEmployee: +_reading.deliveredMoneyByEmployee,
				employeeId: +_reading.userId,
				shiftId: +_reading.shift,
				discounts: +_reading.discounts,
				coupons: +_reading.coupons,
				oils: +_reading.oils,
				checks: +_reading.checks,
				readingDate,
				gpReadings: readings
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _reading, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة القراءة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
